.slider-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  h1 {
    font-size: 50px;
    @media (max-width: 600px) {
      h1 {
        font-size: 40px;
      }
    }
    @media (max-width: 476px) {
      font-size: 32px;
    }
  }
  p {
    text-align: center;
    font-size: 25px;
    margin: 0 70px;
    width: 50%;
    @media (max-width: 476px) {
      text-align: center;
      margin: 10px;
      font-size: 20px;
      width: 90%;
    }
  }

  .slider-button {
    text-align: center;
    width: 180px;
    padding: 10px;
    text-transform: uppercase;
    background-color: rgb(183, 193, 18);
    color: white;
    font-size: 30px;
    font-weight: 500;
    border-radius: 10px;
    margin: 10px 0;
    border: none;
    cursor: pointer;
    transition: ease-in-out 0.3s;
    &:hover {
      transform: scale(1.1);
    }
  }
  a {
    color: white;
    text-decoration: none;
  }
}

.rec.rec-arrow {
  position: absolute;
  margin: 20px;
  z-index: 2;
  background-color: rgba(255, 253, 253, 0.5);
  &:hover:enabled,
  &:focus:enabled {
    background-color: rgba(255, 253, 253, 0.5);
  }
}

.rec-arrow-right {
  right: 0;
}

.rec-pagination {
  position: absolute;
  bottom: 30px;
}

button.rec-dot:hover,
button.rec-dot:active,
button.rec-dot:focus {
  background-color: #fff;
  box-shadow: 0 0 1px 3px rgba(255, 253, 253, 0.7);
}
button.rec-dot {
  width: 20px;
  height: 20px;
  background-color: rgba(255, 253, 253, 0.5);
  box-shadow: 0 0 1px 2px rgba(255, 253, 253, 0.7);
  margin: 10px;
}

.rec-carousel-wrapper {
  position: absolute;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.rec .rec-slider-container {
  margin: 0;
}

.slider-item {
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

@media (max-width: 600px) {
  .rec.rec-arrow {
    display: none;
  }
}
